import React, { useState, useEffect } from 'react';
import axios from 'axios';

const loadingMessage = {
  color: 'rgb(95, 39, 205)',
  fontSize:'38px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  height:'500px',
}
function Gangwon(props) {

    const [users, setUsers] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);


  useEffect(() => {
    const fetchUsers = async () => {
      try {
        //     error  users  
        setError(null);
        setUsers(null);
        // loading  true  .
        setLoading(true);
        const response = await axios.get(
          'https://grpc-proxy-server-mkvo6j4wsq-du.a.run.app/v1/regcodes?regcode_pattern=42*000000&is_ignore_zero=true'
        );
          setUsers(response.data.regcodes); //  response.data  .
      } catch (e) {
        setError(e);
      }
      setLoading(false);
    };

    fetchUsers();
  }, []);

  if (loading) return <div><p style={loadingMessage}><i class="fa-solid fa-spinner"></i></p></div>;
  if (error) return <div><p style={loadingMessage}><i class="fa-solid fa-triangle-exclamation"></i></p></div>;
  if (!users) return null;
  return (
    <div>
      {users.map(user => (
        <button onClick={() => props.setData(user.name)}>{user.name}</button>
      ))}
    </div>
  );
}

export default Gangwon;