import React, { useState, useEffect } from "react";
import axios from "axios";

const loadingMessage = {
  color: "rgb(95, 39, 205)",
  fontSize: "38px",
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  height: "500px",
};
function Seoul(props) {
  const [users, setUsers] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // console.log(users);

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        //     error  users  
        setError(null);
        setUsers(null);
        // loading  true  .
        setLoading(true);
        const response = await axios.get(
          "http://ec2-13-209-237-25.ap-northeast-2.compute.amazonaws.com:8081/reg/sigungu/11"
        );
        setUsers(response.data); //  response.data  .
      } catch (e) {
        setError(e);
      }
      setLoading(false);
    };

    fetchUsers();
  }, []);

  if (loading)
    return (
      <div>
        <p style={loadingMessage}>
          <i class="fa-solid fa-spinner"></i>
        </p>
      </div>
    );
  if (error)
    return (
      <div>
        <p style={loadingMessage}>
          <i class="fa-solid fa-triangle-exclamation"></i>
        </p>
      </div>
    );
  if (!users) return null;
  return (
    <div>
      {users.map((user) => (
        <button onClick={() => props.setData(user.regionName)}>
          {user.regionName}
        </button>
      ))}
    </div>
  );
}

export default Seoul;
